.odd-increased:before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    border-width: 10px 0 0 10px;
    border-style: solid;
    border-color: #2db343 transparent;
    -webkit-animation-name: increase;
            animation-name: increase;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    opacity: 0;
}

.odd-decreased:before {
    content: '';
    position: absolute;
    bottom: 0;
    right: 0;
    border-width: 0 0 10px 10px;
    border-style: solid;
    border-color: #ff3b30 transparent;
    -webkit-animation-name: decrease;
            animation-name: decrease;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    opacity: 0;
}

.odd-increased {
    position: relative;
}

.odd-decreased {
    position: relative;
}

@-webkit-keyframes increase {
    0% {
        opacity: 1;
    }
    
    100% {
        opacity: 1;
    }
}

@keyframes increase {
    0% {
        opacity: 1;
    }
    
    100% {
        opacity: 1;
    }
}

@-webkit-keyframes decrease {
    0% {
        opacity: 1;
    }
    
    100% {
        opacity: 1;
    }
}

@keyframes decrease {
    0% {
        opacity: 1;
    }
    
    100% {
        opacity: 1;
    }
}
